import React, { Component } from "react";
import { BrowserRouter as Router, Routes, Route, Link, Navigate } from "react-router-dom";
import { Container, Navbar, Nav, NavDropdown } from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import config from './config';
import AuthService from "./services/auth.service";
import _t from "./services/i18n.service";

import ACL from "./services/acl.service";
import Unauthorized from "./components/unauthorized.component";
import Login from "./components/login.component";
import Register from "./components/register.component";
import Language from "./components/language.component";

import Layout from "./components/layout.component";
import Professor from "./components/professor/professor.component";
import Student from "./components/student/student.component";
import Stats from "./components/student/stats.component";
import DelUser from "./components/student/del_user.component";
import NotFound from "./components/notfound.component";


class App extends Component {

  constructor(props) {

    super(props);
    this.logOut = this.logOut.bind(this);
    this.logIn = this.logIn.bind(this);
    this.onChangeLang = this.onChangeLang.bind(this);
    this.delUser = this.delUser.bind(this);

    this.state = {user: AuthService.getCurrentUser()};
    window.$lang = this.state.user.lang;
  }

  componentDidMount() {
    this.setState({user: AuthService.getCurrentUser()});
    window.$lang = this.state.user.lang;
  }

  componentDidUpdate() {
    window.$lang = this.state.user.lang;
  }

  logIn() {
    const user = AuthService.getCurrentUser();
    this.setState({user: user});
  }
  
  logOut() {
    AuthService.logout();
    this.setState({user: AuthService.getCurrentUser()});
  }

  onChangeLang(lang) {
    window.$lang = lang;
    this.setState({user: {...this.state.user, lang: lang}});
    localStorage.setItem('user', JSON.stringify(this.state.user));
    // TODO: update language on server 
  }

  delUser(successful) {
    if (successful)  {
      this.logOut();
    }
  }

  render() {    
    const user = this.state.user;

    return (
      <Router>
        <Routes>
          <Route path='/' element={<Layout user={user} logOut={this.logOut}/>} >
            <Route index element={
              (user.role && user.role === 'ROLE_PROFESSOR')  
              ? (<Navigate to="/prof" replace />)
              : (user.role && user.role === 'ROLE_STUDENT') 
              ? (<Navigate to="/stu" replace />)
              : (<Navigate to="/login" replace />)
            }/>
            <Route path="login" element={<Login cb={this.logIn} onChangeLang={this.onChangeLang}/>} />
            <Route path="unauthorized" element={<Unauthorized />} />
            <Route path="register" element={<Register user={user} onChangeLang={this.onChangeLang}/>} />
            <Route path="lang" element={<Language onClick={this.onChangeLang}/>} />
            
            <Route path="prof" element={
              <ACL roles={['ROLE_PROFESSOR']}>
                <Professor user={user}/>
              </ACL> 
            }/>
            
            <Route path="stu" element={
              <ACL roles={['ROLE_STUDENT']}>
                <Student user={user}/>
              </ACL>    
            }/>

            <Route path="stu-stats" element={
              <ACL roles={['ROLE_STUDENT']}>
                <Stats user={user}/>
              </ACL>    
            }/>

            <Route path="del-user" element={
              <ACL roles={['ROLE_STUDENT']}>
                <DelUser user={user} delUser={this.delUser}/>
              </ACL>    
            }/>

            <Route path="*" element={<NotFound />} />
          </Route>
        </Routes>
      </Router>
    );
  }
}

export default App;