import React from "react";
import { BrowserRouter as Router, Routes, Route, Link, Navigate, Outlet } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Navbar, Nav, NavDropdown } from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import config from '../config';
import _t from "../services/i18n.service";

function Layout (props) {
    const user = props.user;

    return (
        <div>
        <Navbar bg="light" expand="lg"> 
            <Container>
                <Navbar.Brand>
                    <Link to="/">
                        &nbsp; levelUp 
                    </Link>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav>
                        {user.role === 'ROLE_PROFESSOR' && (

                            <LinkContainer to="/prof" onClick={() => {window.location = './prof'}}>
                                <Nav.Link>
                                <i className="bi bi-easel"></i> {_t('classrooms_title')}
                                </Nav.Link>
                            </LinkContainer>
                        )}

                        {user.role === 'ROLE_STUDENT' && (
                            <>
                            <LinkContainer to="/stu" onClick={() => {window.location = './stu'}}>
                                <Nav.Link> 
                                <i className="bi bi-easel"></i> {_t('classrooms_title')}
                                </Nav.Link>
                            </LinkContainer>

                            <LinkContainer to="/stu-stats">
                                <Nav.Link>
                                <i className="bi bi-clipboard-data"></i> {_t('stats_title')}
                                </Nav.Link>
                            </LinkContainer>

                            <LinkContainer to="/del-user">
                                <Nav.Link>
                                <i className="bi bi-trash"></i> {_t('delete_user')}
                                </Nav.Link>
                            </LinkContainer>
                            </>
                        )}
                    </Nav>
                    <Nav>
                        <NavDropdown.Divider />
                        {user.email ? (
                            <>
                            <LinkContainer to="/lang">
                                <Nav.Link>
                                <i className="bi bi-translate"></i> {_t('link_language')}
                                </Nav.Link>
                            </LinkContainer>

                            <Nav.Link href="/login" onClick={props.logOut}>
                                <i className="bi bi-box-arrow-left"></i> {_t('link_logout')}
                            </Nav.Link>
                            </>
                        ) : (
                            <>
                            <LinkContainer to="/login">
                            <Nav.Link>
                                <i className="bi bi-box-arrow-in-right"></i> {_t('link_login')}
                            </Nav.Link>
                            </LinkContainer>

                            <LinkContainer to="/register">
                            <Nav.Link>
                                <i className="bi bi-person-plus"></i> {_t('link_register')}
                            </Nav.Link>
                            </LinkContainer>
                            </>
                        )}
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>

        <div className="container-fluid mx-1 mt-2">
            <Outlet />
            {props.children}
        </div>
        </div>
    );
}

export default Layout;